import React from 'react'
import { setGenre } from "../redux/slices/filterSlice";
import { useSelector, useDispatch } from "react-redux";
import { useState } from 'react';

function Genres({setCurrentPage}) {
  const dispatch = useDispatch()
  const genreValue = useSelector(state => state.filter.genre)
  const [spread, setSpread] = useState(false)
  const setGenreValue = (id) => {
      dispatch(setGenre(id))
  }
  const spreadRef = React.useRef()
  const onClickButton = () => {
    setSpread(!spread)
  }

  document.body.addEventListener('click', (e) => {
    if (!e.composedPath().includes(spreadRef.current)) {
      setSpread(false)
    }
  })

  const genres = [
    {name: '', genreProperty: 'all'},
    {name: '', genreProperty: 'action'},
    {name: '', genreProperty: 'rpg'},
    {name: '', genreProperty: 'shooter'},
    {name: '', genreProperty: 'strategy'},
    {name: '', genreProperty: 'puzzle'},
    {name: '', genreProperty: 'horror'},
    {name: '', genreProperty: 'platformer'},
    {name: '', genreProperty: 'roguelike'},
    {name: '', genreProperty: 'metroidvania'},
    {name: '', genreProperty: 'adventure'},
    {name: '', genreProperty: 'racing'},
    {name: '', genreProperty: 'sandbox'},
  ]
    return (
        <div className="genres__holder">
          {genres
          .slice(0,5)
          .map((genre) => (
              <a key={genre.name} onClick={() => {
                setGenreValue(genre);
                setCurrentPage(0)}} 
               className = {genreValue.genreProperty == genre.genreProperty ? 'genre-btn checked-genre' : 'genre-btn'}>{genre.name}</a>
          ))}
          <div ref={spreadRef} className="genres__spread">
            <div onClick={onClickButton} className="genres__button"></div>
            <div className={spread ? "genres__ul" : "genres__ul genres__ul_hide"}>
            <ul>
              {genres
              .slice(5)
              .map((genre) => (
                <a key={genre.name} onClick={() => {
                  setGenreValue(genre);
                  setCurrentPage(0);
                  setSpread(false)}} 
                 className = {genreValue.genreProperty == genre.genreProperty ? 'genre-btn checked-genre' : 'genre-btn'}>{genre.name}</a>
            ))}
            </ul>
            </div>
          </div>
        </div>
    )
  }

export default Genres